import React, { Component } from "react";
import { connect } from "react-redux";
import * as actions from "../../../store/actions";
import CardLayout from "../../../components/Cards/CardLayout";
import { FormGroup, Col, Button, Label } from "reactstrap";
import DropdownSelect from "../../../components/InputElement/Dropdown";
import InputElement from "../../../components/InputElement/InputElement";
class DistrictForm extends Component {
  constructor(props) {
    super(props);
    this.state = {
      updateFlag: false,
      districtObj: {
        DistrictName: "",
        StateName: ""
      }
    };
  }
  componentWillMount() {
    this.props.getStatesList();
  }
  componentDidMount() {
    if (this.props.match.params.id !== undefined) {
      let currentDistrict = this.props.districts.find(
        district => district.Id == this.props.match.params.id
      );
      // if (currentDistrict) {
      //   let districtObj = {...this.state.districtObj};
      //   districtObj.DistrictName = currentDistrict;
      this.setState({
        updateFlag: true
      });
      // }
    }
  }
  onStateSelect(value) {
    let districtObj = { ...this.state.districtObj };
    districtObj.StateName = value;
    this.setState({
      districtObj: districtObj
    });
  }
  onSubmit() {
    this.props.history.push("/master/districts");
  }
  render() {
    const districtObj = { ...this.state.districtObj };
    const valueSelect = this.state.selectedDistrict;
    return (
      <div style={{ marginTop: 30 }}>
        <CardLayout
          name="District Form"
          navigation={true}
          navigationRoute="/master/districts"
        >
          <FormGroup row />
          <div style={{ margin: 20 }}>
            <FormGroup row>
              <Col xs="10" md="5">
                <Label />
                <DropdownSelect
                  placeholder="Please select state"
                  options={this.props.statesList}
                  onChange={this.onStateSelect.bind(this)}
                  value={districtObj.StateName}
                />
              </Col>
              <Col xs="10" md="5">
                <InputElement
                  type="text"
                  placeholder="Please enter district"
                  value={districtObj.DistrictName}
                />
              </Col>
            </FormGroup>
            <FormGroup row>
              <Col md="3">
                {this.state.updateFlag ? (
                  <Button
                    className="theme-positive-btn"
                    onClick={this.onSubmit.bind(this)}
                  >
                    Edit
                  </Button>
                ) : (
                  <Button
                    className="theme-positive-btn"
                    onClick={this.onSubmit.bind(this)}
                  >
                    Submit
                  </Button>
                )}
              </Col>
            </FormGroup>
          </div>
        </CardLayout>
      </div>
    );
  }
}
const mapStateToProps = state => {
  return {
    districtsList: state.districtReducer.districtsList,
    districts: state.districtReducer.districts,
    statesList: state.stateReducer.statesList
  };
};

const mapDispatchToProps = dispatch => {
  return {
    getStatesList: () => dispatch(actions.getStatesList())
  };
};
export default connect(mapStateToProps, mapDispatchToProps)(DistrictForm);



